import config from '../config';

const PeopleService = {
  getPeople() {
    return fetch(`${config.REACT_APP_API_BASE}/people`, {
      method: 'GET',
      headers: {
          'content-type': 'application/json',
      }
    })
    .then(res =>
      (!res.ok)
        ? res.json().then(e => Promise.reject(e))
        : res.json()
    );
  },
  postPerson(person) {
    return fetch(`${config.REACT_APP_API_BASE}/people`, {
      method: 'POST',
      headers: {
          'content-type': 'application/json',
      },
      body: JSON.stringify({person}),
    })
    .then(res => 
      (!res.ok)
        ? res.json().then(e => Promise.reject(e))
        : null
    );
  },
}

export default PeopleService;