import React from 'react';
import {Link} from 'react-router-dom';
import './LandingPage.css';

export default class LandingPage extends React.Component {
  render() {
    return (
      <div className="wrapper">
        <h1>Petful</h1>
        <div className="site-description">
          <img
            src="http://www.dogster.com/wp-content/uploads/2015/05/Cute%20dog%20listening%20to%20music%201_1.jpg"
            alt="dog"
          />
          <div className="adoption-info-text">
            {
            `Find your new family member today!\nApply to adopt a cat or dog.\n
            Note: You can apply for either a cat or dog or both. 
            However, you may only adopt the pet that has been here the longest.`}
          </div>
          <Link to='/adoption'>Apply!</Link>
        </div>
      </div>
    );
  }
}
