import config from '../config';

const PetsService = {
  getPets() {
    return fetch(`${config.REACT_APP_API_BASE}/pets`, {
      method: 'GET',
      headers: {
          'content-type': 'application/json',
      }
    })
    .then(res =>
      (!res.ok)
        ? res.json().then(e => Promise.reject(e))
        : res.json()
    );
  },
  deletePet(type) {
    return fetch(`${config.REACT_APP_API_BASE}/pets`, {
      method: 'DELETE',
      headers: {
          'content-type': 'application/json',
      },
      body: JSON.stringify({type}),
    })
    .then(res =>
      (!res.ok)
        ? res.json().then(e => Promise.reject(e))
        : null
    );
  },
}

export default PetsService;