import React from 'react';

export default class Pet extends React.Component {

  static defaultProps = {pet: {}};

  render() {
    const {age, breed, description, gender, imageURL, name, story} = this.props.pet;
    return (
      <div className='pet'>
        <h2>{name}</h2>
        <div className='info'>
          <p>Age: {age}</p>
          <p>Breed: {breed}</p>
          <p>Gender: {gender}</p>
        </div>
        <img src={imageURL} alt='pet for adoption'/>
        <p>Description: {description}</p>
        <p>Story: {story}</p>
      </div>
    );
  }
}